:root {
  --color-form: #fff6da;
  --color-log: #f3ecff;
  --color-card: #c8fcea;
  --color-dark: #484554;
  --color-gray: #ccc;
  --color-focus: #4635b1;
  --color-success: #16c47f;
  --color-danger: #ff8383;
  --base-font-size: 16px;
}

*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: var(--color-dark);
  font-family: Verdana, sans-serif;
  font-size: var(--base-font-size);
}

*, :focus {
  outline: none;
}

.header {
  margin-bottom: 1rem;
  padding: 2rem 0;
}

.header h1 {
  text-align: center;
}

.container {
  margin: 0 auto;
  padding: 0 1rem;
}

.container__column {
  flex-direction: column;
  row-gap: 1.5rem;
  display: flex;
}

.container__border {
  border: 1px solid var(--color-dark);
  border-radius: 15px;
  padding: 1rem;
}

.form--record {
  background: var(--color-form);
  max-width: 43rem;
  margin: 0 auto;
}

.form--log {
  background-color: var(--color-log);
}

.form--log .form__item--select {
  width: 100%;
  max-width: 40rem;
  margin: 0 auto;
}

.form__label {
  padding: 0 10px;
  font-weight: 700;
}

.form__item--radio {
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  display: flex;
}

.form__item--input {
  flex-direction: column;
  row-gap: .5rem;
  display: flex;
}

.form__item--select {
  align-items: center;
  column-gap: 1rem;
  display: flex;
}

.form__input {
  border: solid 1px var(--color-gray);
  background: none;
  border-radius: 10px;
  flex-grow: 1;
  height: 50px;
  padding: 0 15px;
  transition: all .3s;
}

.form__input:focus, .form__select:focus {
  border-color: var(--color-focus);
}

.form__select {
  border: solid 1px var(--color-gray);
  background: none;
  border-radius: 10px;
  flex-grow: 1;
  height: 50px;
  padding: 0 15px;
  transition: all .3s;
}

.form__control {
  justify-content: center;
  align-items: center;
  column-gap: 2rem;
  display: flex;
}

.btn {
  border-radius: 10px;
  width: 5rem;
  padding: .5rem 0;
  font-weight: 700;
}

.btn--success {
  background-color: var(--color-success);
}

.btn--danger {
  background-color: var(--color-danger);
}

#logger {
  grid-template-columns: 1fr;
  justify-items: center;
  gap: 1rem;
  display: grid;
}

.book-card {
  background-color: var(--color-card);
  border: 1px solid #000;
  border-radius: 15px;
  justify-content: space-between;
  gap: 2rem;
  width: 100%;
  max-width: 25rem;
  padding: 1rem;
  display: flex;
}

.book-control {
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.error-msg {
  color: var(--color-danger);
  padding: 0 1rem;
  font-weight: 700;
}

.error-msg--hidden {
  visibility: hidden;
}

#snackbar {
  visibility: hidden;
  min-width: 16rem;
  color: var(--color-dark);
  text-align: center;
  z-index: 1;
  border-radius: 10px;
  padding: 1rem 2rem;
  position: fixed;
  top: 5rem;
  right: 0;
  transform: translateX(-10%);
}

#snackbar.show--success {
  visibility: visible;
  background-color: var(--color-success);
}

#snackbar.show--danger {
  visibility: visible;
  background-color: var(--color-danger);
}

@media only screen and (width >= 768px) {
  .container {
    max-width: 720px;
    padding: 0 1rem;
  }
}

@media only screen and (width >= 992px) {
  .container {
    max-width: 960px;
  }

  #logger {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (width >= 1200px) {
  .container {
    max-width: 1140px;
    padding: unset;
  }

  #logger {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
